<template>
  <div ref="scrollwrapper">
    <slot></slot>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  props: {
    /**
     * 1 scroll
     * 2 scroll
     * 3 scrollswipescroll
     */
    probeType: {
      type: Number,
      default: 1
    },
    eventPassthrough: {
      type: String,
      default: 'horizontal'
    },
    // click
    click: {
      type: Boolean,
      default: true
    },
    // 
    scrollX: {
      type: Boolean,
      default: false
    },
    scrollY: {
      type: Boolean,
      default: true
    },
    // 
    listenScroll: {
      type: Boolean,
      default: false
    },
    // 
    data: {
      type: Array,
      default: null
    },
    // 
    pullup: {
      type: Boolean,
      default: false
    },
    // 
    pulldown: {
      type: Boolean,
      default: false
    },
    // 
    beforeScroll: {
      type: Boolean,
      default: false
    },
    // scroll
    refreshDelay: {
      type: Number,
      default: 20
    },
    // 
    scrollToOrigin: {
      type: Boolean,
      default: false
    },
    scrollElement: {
      type: String
    }
  },
  mounted () {
    this._initScroll()
    // data
    if (this.$refs.scrollwrapper.children[0].children.length) {
      this.$nextTick(() => {
        this.refresh()
      })
    }
  },
  watch: {
    data () {
      setTimeout(() => {
        this.refresh()
        if (this.scrollToOrigin) {
          this.scrollTo(0, 0, 100, 'ease')
        }
      }, this.refreshDelay)
    },
    scrollElement (newval) {
      let el = document.getElementsByClassName(newval)[0]
      let width = document.body.clientWidth || document.documentElement.clientWidth
      let selfwidth = el.clientWidth
      this.scrollToElement(el, 300, (selfwidth - width) / 2)
    }
  },
  methods: {
    _initScroll () {
      if (this.$refs.scrollwrapper) {
        this.scroll = new BScroll(this.$refs.scrollwrapper, {
          probeType: this.probeType,
          click: this.click,
          scrollX: this.scrollX,
          scrollY: this.scrollY
        })
        // 
        if (this.listenScroll) {
          this.scroll.on('scroll', (pos) => {
            this.$emit('scroll', pos)
          })
        }
        // 
        if (this.pullup) {
          this.scroll.on('scrollEnd', () => {
            if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
              this.$emit('get-more-date')
            }
          })
        }
        // 
        if (this.pulldown) {
          this.scroll.on('touchEnd', (pos) => {
            if (pos.y > 50) {
              this.$emit('reset-date')
            }
          })
        }
        // 
        if (this.beforeScroll) {
          this.scroll.on('beforeScrollStart', () => {
            this.$emit('before-scroll')
          })
        }
      }
    },
    disable () {
      this.scroll && this.scroll.disable()
    },
    enable () {
      this.scroll && this.scroll.enable()
    },
    refresh () {
      if (this.scrollX) {
        this.$nextTick(() => {
          this._setWidth()
        })
      }
      this.scroll && this.scroll.refresh()
    },
    _setWidth () {
      let width = 0
      let wapperul = this.$refs.scrollwrapper.children[0]
      for (let i = 0; i < wapperul.children.length; i++) {
        let dom = wapperul.children[i]
        let domWidth = parseFloat(getComputedStyle(dom).marginRight) + parseFloat(getComputedStyle(dom).marginLeft)
        width = width + dom.offsetWidth + domWidth
      }
      wapperul.style.width = width + 'px'
    },
    scrollTo () {
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
    },
    scrollToElement () {
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
    }
  }
}
</script>
